.steps {
  padding: 120px 0;
  background-color: #fff;

  &__title {
    margin: 0 auto;
    margin-bottom: 57px;
    max-width: 720px;
    font-family: Roboto;
    font-weight: 400;
    font-size: 30px;
    line-height: 38px;
    letter-spacing: 1.2px;
    text-align: center;
    color: #003331;

    @include mobile {
      font-size: 27px;
      line-height: 30px;
    }

    @include mini-mobile {
      font-size: 23px;
      line-height: 25px;
    }
  }

  &__list {
    counter-reset: steps;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 758px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;

    &::before {
      counter-increment: steps;
      content: counter(steps);
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      border-radius: 100%;
      background: #014c48;
      color: #fff;
      font-family: "Public Sans";
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      margin-bottom: 28px;
    }

    &-line {
      flex-grow: 1;
      position: relative;

      &::before {
        display: none;
      }

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: 1px;
        margin-top: -35px;
        transform: translateY(-50%);
        background: rgba(53, 83, 98, 0.20);
      }
    }
  }

  &__descr {
    margin: 0;
    max-width: 142px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    text-align: center;
    color: rgba(53, 83, 98, 0.8);

    @include mini-mobile {
      font-size: 13px;
      line-height: 19px;
    }
  }
}
