.hero {
  display: flex;
  flex-direction: column;
  padding-top: 190px;
  padding-bottom: 40px;
  min-height: 776px;
  color: #fff;
  background-image: url("../img/hero-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__content {
    grid-column: 6 span;

    @include mobile {
      grid-column: 3 span;
    }
  }

  &__container {
    margin-bottom: 32px;
  }

  &__title {
    font-family: Roboto;
    margin: 0;
    margin-bottom: 29px;
    font-size: 38px;
    font-weight: 400;

    @include tablet {
      font-size: 32px;
      line-height: 40px;
    }

    @include small-tablet {
      font-size: 28px;
      line-height: 32px;
    }

    @include mobile {
      font-size: 23px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 29px;
    font-family: "Public Sans";
    font-size: 18px;
    font-style: normal;
    font-weight: 400;

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__bottom-container {
    margin-top: auto;
    width: 100%;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 15px;
    color: #fff;
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
  }
  &__item {
    grid-column: 2 span;
  }
  &__link {
    display: block;
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    color: #fff;
    padding-top: 17px;
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }
    @include hover {
      border-color: #04403d;
      color: #014c48;
    }
    &:active {
      border-color: #04403d;
      color: #014c48;
    }

    @include mobile {
      padding-bottom: 10px;
    }
  }
}
.hero__link.focus-visible {
  border-color: #014c48;
}
