.info {
  display: flex;
  position: relative;
  background: #001414;
  height: 680px;

  @include small-tablet {
    height: 550px;
  }

  @include mini-mobile {
    height: 480px;
  }

  &::before {
    content: "";
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url("../../img/info-1.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include tablet{
      width: 180px;
    }

    @include small-tablet {
      width: 130px;
    }

    @include mobile {
      display: none;
    }
  }

  &::after {
    content: "";
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url("../../img/info-2.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include tablet{
      width: 180px;
    }

    @include small-tablet {
      width: 130px;
    }

    @include mobile {
      display: none;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
    align-items: center;
    border-left: 1px solid #003331;
    border-right: 1px solid #003331;
    padding: 0 82px;
    height: 100%;

    @include mini-mobile {
      padding: 0 40px;
    }
  }

  &__text {
    max-width: 542px;
  }

  &__title {
    margin: 0;
    margin-bottom: 21px;
    color: #fff;
    font-family: "Public Sans";
    font-size: 22px;
    font-style: normal;
    font-weight: 400;

    @include tablet{
      font-size: 20px;
    }
  }

  &__descr {
    margin: 0;
    color: #fff;
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;

    @include tablet{
      font-size: 12px;
    }
  }
}
