.press {
  background: #fff;
  padding: 87px 0;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 33px;
  }

  &__title {
    margin: 0;
    color: #003331;
    font-family: "Roboto";
    font-size: 27px;
    font-style: normal;
    font-weight: 400;

    @include mini-mobile {
      font-size: 17px;
    }
  }

  &__list {
    row-gap: 32px;
  }

  &__btn {
    color: #00413e;
    text-align: center;
    font-family: "Public Sans";
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    border-radius: 80px;
    border: 1px solid rgba(53, 83, 98, 0.2);

    @include hover {
      color: white;
    }

    @include mini-mobile {
      padding: 12px 29px;
    }
  }

  &__item {
    grid-column: 4 span;
  }
}
.press-card {
  min-height: 145px;
  position: relative;

  &__top {
    display: flex;
    color: rgba(53, 83, 98, 0.6);
    font-family: "Public Sans";
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
    gap: 16px;
    margin-bottom: 11px;
    border-top: 1px solid rgba(53, 83, 98, 0.3);
    color: rgba(53, 83, 98, 0.8);
    padding-top: 20px;
    margin-top: -20px;
    padding-bottom: 8px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    background-position: left top 16px;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    z-index: 99;
  }

  &__title {
    margin: 0;
    color: #00413e;
    font-family: "Public Sans";
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
  }

  &__author {
    margin: -22px 0;
    padding: 22px 0;
    padding-left: 32px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    background-position: left top 16px;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    z-index: 99;

    &-ted {
      background-image: url(../../img/approach__svg-1.svg);
    }
    &-nyt {
      background-image: url(../../img/approach__svg-2.svg);
    }
    &-scoop {
      background-image: url(../../img/approach__svg-3.svg);
    }
    &-press {
      background-image: url(../../img/approach__svg-4.svg);
    }
    &-time {
      background-image: url(../../img/approach__svg-5.svg);
    }
    &-freethink {
      background-image: url(../../img/approach__svg-6.svg);
    }
  }

  &__link {
    color: inherit;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }
    @include hover {
      border-color: #539b97;
      color: #539b97;
    }
    &:active {
      border-color: #539b97;
      color: #539b97;
    }

    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }
  }
}
