.footer {
  &__top {
    padding-top: 112px;
    padding-bottom: 137px;
    background: #001414;

    @include mobile {
      padding: 75px 0;
    }
  }

  &__bottom {
    padding-top: 40px;
    padding-bottom: 63px;
    background: #000f0f;

    @include mini-mobile {
      padding-bottom: 30px;
    }

    &-container {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @include big-desktop {
        justify-content: space-around;
      }
    }
  }

  &__column {
    grid-column: 2 span;

    @include mobile {
      grid-column: 1 span;
    }

    &:last-child {
      grid-column: 11 / 2 span;

      @include mobile {
        grid-column: 5 / 1 span;
      }

      @include mini-mobile {
        margin-top: 20px;
        grid-column: 4 span;
      }
    }
  }

  &__link {
    color: #edeceb;
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }
    @include hover {
      border-color: #48908c;
      color: #48908c;
    }
    &:active {
      border-color: #48908c;
      color: #48908c;
    }
  }

  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 23px;
    padding-bottom: 24px;
    color: #7d9798;
    font-family: "Public Sans";
    font-size: 13px;
    font-style: normal;
    font-weight: 400;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 1px;
      background: #25b895;
    }
  }

  &__logo {
    @include big-desktop {
      max-width: 140px;
    }
  }

  &__sublist {
    display: flex;
    flex-direction: column;
    font-family: "Public Sans";
    gap: 8px;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;

    &-link {
      color: #e5ecec;
      transition: color 0.3s ease-in-out;

      &:focus {
        outline: none;
      }
      @include hover {
        border-color: #48908c;
        color: #48908c;
      }
      &:active {
        border-color: #48908c;
        color: #48908c;
      }
    }

    &-wrapper {
      display: flex;
      gap: 8px;
    }
  }
}
