.approach {
  &__top {
    padding: 80px;
    background: #f7f8f8;

    @include small-tablet {
      padding: 60px;
    }
  }

  &__left {
    padding-top: 33px;
    grid-column: 5 span;

    @include big-desktop {
      margin-bottom: 32px;
      text-align: center;
      grid-column: 5 / 5 span;
    }

    @include small-tablet {
      padding-top: 0;
      text-align: center;
      grid-column: 4 / 7 span;
    }

    @include mobile {
      grid-column: 12 span;
    }

    @include mini-mobile {
      grid-column: 7 span;
    }
  }

  &__right {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    grid-column: 7 / 6 span;
    gap: 8px;

    @include big-desktop {
      grid-column: 12 span;
      justify-content: center;
    }

    @include mobile {
      grid-column: 3 / 8 span;
    }

    @include mini-mobile {
      grid-column: 2 / 5 span;
    }
  }

  &__img{
    max-width: 100%;
  }

  &__descr {
    margin: 0;
    margin-bottom: 37px;
    color: rgba(53, 83, 98, 0.8);
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;

    @include mobile {
      font-size: 13px;
    }

    @include mini-mobile {
      font-size: 11px;
    }
  }

  &__btn {
    border: 1px solid rgba(53, 83, 98, 0.2);
    color: #26625f;
    text-align: center;
    font-family: "Public Sans";
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    transition: background-color 0.3s ease-in-out;
  }

  &__btn:hover {
    color: #fff;
    background-color: #014c48;
  }

  &__bottom {
    background: #fff;
    padding: 24px;

    @include mobile {
      padding: 14px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;
    color: #003331;
    font-family: "Public Sans";
    font-size: 22px;
    font-style: normal;
    font-weight: 400;

    @include mobile {
      font-size: 17px;
    }

    @include mini-mobile {
      font-size: 16px;
    }
  }

  &__item {
    grid-column: 2 span;
  }

  &__link {
    display: block;
    border-top: 1px solid rgba(53, 83, 98, 0.3);
    color: rgba(53, 83, 98, 0.8);
    padding-left: 32px;
    padding-top: 20px;
    padding-bottom: 6px;
    font-family: "Public Sans";
    font-size: 15px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    background-position: left top 16px;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    z-index: 99;

    &-ted {
      background-image: url(../../img/approach__svg-1.svg);
    }
    &-nyt {
      background-image: url(../../img/approach__svg-2.svg);
    }
    &-scoop {
      background-image: url(../../img/approach__svg-3.svg);
    }
    &-press {
      background-image: url(../../img/approach__svg-4.svg);
    }
    &-time {
      background-image: url(../../img/approach__svg-5.svg);
    }
    &-freethink {
      background-image: url(../../img/approach__svg-6.svg);
    }

    &:focus {
      outline: none;
    }
    @include hover {
      border-color: #04403d;
      color: #014c48;
    }
    &:active {
      border-color: #04403d;
      color: #014c48;
    }

    @include small-tablet {
      font-size: 10px;
      padding-bottom: 11px;
    }

    @include mobile {
      padding-bottom: 20px;
    }
  }
}
