.header {
  padding: 17px 0;
  &__container {
    align-items: center;
    justify-content: space-between;
  }
  &--position {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }
}
.nav {
  align-items: center;
  padding-right: 16px;

  &__list {
    align-items: center;
    margin-right: 32px;
    gap: 51px;

    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
      margin-right: 0;
    }
  }
  &__link {
    color: #fff;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: #014c48;
    }

    &:active {
      color: #014c48;
    }

    @include small-tablet {
      font-size: 16px;
    }
  }

  @include small-tablet {
    position: fixed;
    left: 0;
    top: 0;
    width: 80%;
    height: 100vh;
    padding: 32px;
    z-index: 100;
    overflow-y: auto;
    background: #001f1f;
    transform: translateX(-100%);
    visibility: hidden;
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  @include small-tablet {
    flex-direction: column;
    align-items: flex-start;
    gap: 51px;
  }
}
.nav__link.focus-visible {
  outline: 1px solid #014c48;
  outline-offset: 3px;
}
.nav.menu--active{
  visibility: visible;
  transform: none;
}