.information {
  margin: 0;
  position: relative;
  padding-bottom: 140px;
  background: #f4f5f5;

  @include mobile {
    padding-bottom: 50px;
  }

  &__text {
    grid-column: 6 span;

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__title {
    margin: 0;
    max-width: 80%;
    color: #003331;
    font-family: "Roboto";
    font-size: 31px;
    font-style: normal;
    font-weight: 400;

    @include small-tablet {
      font-size: 25px;
    }

    @include mobile {
      font-size: 19px;
      margin-bottom: 25px;
    }
  }

  &__list {
    grid-column: 7 / 6 span;
    display: grid;
    row-gap: 36px;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 32px;

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__item {
    grid-column: 3 span;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 4px;
    color: #003331;
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
  }

  &__descr {
    margin: 0;
    color: rgba(53, 83, 98, 0.8);
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;

    @include small-tablet {
      font-size: 13px;
    }
  }
}
