.partners {
  padding: 16px 0;
  background: #fff;

  &__container {
    gap: 6px;
    align-items: center;

    @include big-desktop {
      justify-content: space-between;
    }

    @include tablet {
      flex-wrap: wrap;
      justify-content: flex-start;
    }
  }

  &__logo {
    @include big-desktop {
      max-width: 130px;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    gap: 13px;

    @include big-desktop {
      justify-content: space-between;
    }

    @include tablet {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__title {
    margin: 0;
    color: rgba(53, 83, 98, 0.6);
    font-family: "Public Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;

    @include big-desktop {
      font-size: 20px;
      margin: 0 auto;
    }
  }

  &__more {
    margin: 0;
    color: rgba(53, 83, 98, 0.6);
    font-family: "Public Sans";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    transition: color 0.3s ease-in-out;

    @include hover {
      color: #014c48;
    }

    @include big-desktop {
      font-size: 20px;
      margin: 0 auto;
    }
  }
}

.partners__link.focus-visible {
  border-color: #014c48;
}
