.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 80px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 17px 33px;
  color: #fff;
  font-family: "Public Sans";
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  transition: background-color 0.3s ease-in-out;
  cursor: pointer;

  &:focus {
    outline: none;
  }
  @include hover {
    border-color: #014c48;
    background-color: #014c48;
  }
  &:active {
    border-color: #014c48;
    background-color: #014c48;
  }
}
.btn.focus-visible{
    border-color: #014c48;
}
