.where {
  padding-top: 155px;
  padding-bottom: 133px;
  color: #fff;
  background: #001f1f;

  @include mobile {
    padding: 60px 0;
  }

  &__content {
    grid-column: 4 span;
  }

  &__map {
    grid-column: 7 / 5 span;

    @include mobile {
      grid-column: 4 span;
      margin-top: 30px;
    }

    &-img {
      @include mini-mobile {
        max-width: 330px;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 43px;
    font-family: "Roboto";
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
  }

  &__descr {
    margin: 0;
    margin-bottom: 37px;
    font-family: "Public Sans";
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
  }
}
